	module.exports = {"data":{"wordpressPost":{"title":"The September TOP 10 Is Here And It&#8217;s INSANELY Good","content":"<p>Look &#8230; here&#8217;s the deal.</p>\n<p><strong>We try and test every single one of the products</strong> that cross our desks each month. And there&#8217;s a LOT. Like, heaps. To be honest it&#8217;s kind of like the grown woman&#8217;s version of the Willy Wonka Chocolate Factory in here.</p>\n<p>There&#8217;s lipsticks, foundations, eye liners, eye creams, hair brushes, hair removers and who knows what else in here. We do our best to keep it catalogued and orderly &#8211; cause, we like a bit of order. But also, because that way we can sift through and really find the very, very best of what&#8217;s out this month.<br />\nAnd we&#8217;ve done it again.<br />\nThat brings us to <strong>The Beauty Insider&#8217;s official Top 10.</strong><br />\nThis is a list of what we regard as the best new products on counter right this very second.<br />\nHow do we know these are the very best of the best? Because we have tried and tested every single one. We&#8217;ve looked at texture, scent, efficacy and of course, bang for buck.</p>\n<figure id=\"attachment_3064\" style=\"width: 300px\" class=\"wp-caption alignnone\"><img class=\"size-medium wp-image-3064\" src=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/09/02201601/CUSHION-HIGHLIGHTER-OLT-FALL-17-300x169.jpg\" alt=\"\" width=\"300\" height=\"169\" srcset=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/09/02201601/CUSHION-HIGHLIGHTER-OLT-FALL-17-300x169.jpg 300w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/09/02201601/CUSHION-HIGHLIGHTER-OLT-FALL-17-768x432.jpg 768w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/09/02201601/CUSHION-HIGHLIGHTER-OLT-FALL-17-1024x576.jpg 1024w\" sizes=\"(max-width: 300px) 100vw, 300px\" /><figcaption class=\"wp-caption-text\">Look who made it into the Top 10 &#8230; click through and learn why.</figcaption></figure>\n<p>&nbsp;</p>\n<p>After all, if you&#8217;re going to pay for something then you want it to do what it should and in a truly awesome way. And we promise, hand on our hearts, that this lot provides all that and more.<br />\nOne thing we think is really important to point out. <strong>Not ONE, nope, NOT ONE, of the beauty houses behind these products has paid to be included on this list. It is 100 per cent independently reviewed by us &#8211; end of story.</strong><br />\nYou&#8217;re a beauty lover (you wouldn&#8217;t be here otherwise) and likely to have your own ideas about what really works and what doesn&#8217;t and even why. If there&#8217;s a product you absolutely adore, please let us know about it.<br />\nWho knows &#8211; perhaps we&#8217;ll love it just as much and it will make next month&#8217;s list.<br />\nBut for now &#8230; let us introduce you to the <a href=\"http://the-beautyinsider.com/top10/\">September Top 10.</a> We know you&#8217;ll love it.</p>\n","better_featured_image":{"source_url":"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/09/02210250/LANCOME-FALL17-CARRE5.jpg"},"categories":[{"slug":"makeup","name":"Makeup"}]},"site":{"siteMetadata":{"title":"The Beauty Insider"}}},"pathContext":{"id":"0d5afd11-e544-5751-8121-9097df4e5aaf"}}

